import React from 'react';
import PropTypes from 'prop-types';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ShowcaseCard from './ShowcaseCard';

import './ShowcaseList.scss';

export default function showcaseList(props) {
  const { showcases } = props;  
  return (
    <div className="showcase-list">
      <Container>
        <Row>
          {
            (showcases || []).map((sc) => <Col key={sc.title} xs={12} sm={12} md={6} lg={6} xl={4}><ShowcaseCard showcase={sc} /></Col>)
          }
        </Row>
      </Container>
    </div>
  );
}
showcaseList.propTypes = {
  showcases: PropTypes.arrayOf(PropTypes.shape({
    cover: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    desc: PropTypes.string.isRequired,
    technologies: PropTypes.arrayOf(PropTypes.string).isRequired,
    partners: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string.isRequired,
      slug: PropTypes.string.isRequired,
    })).isRequired
  })),
}
