import React from 'react';
import logo from '../../logo.png';

export default function Header() {
  return (
    <div>
      <img src={logo} className="App-logo" alt="logo" />
      <p>
        Some <code>Showcases</code> are private, hence They'll not be attached here!
      </p>
      <a
        className="App-link"
        href="https://brilacasck.github.io"
      >
        Brilacasck
      </a>
      <hr />
      <a href="#main"><span role="img" aria-label="go-down"><i className="fas fa-angle-down"></i></span></a>
    </div>
  );
}
