import React from 'react';
import PropTypes from 'prop-types';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import ListGroupItem from 'react-bootstrap/ListGroupItem';
import Button from 'react-bootstrap/Button';

import './ShowcaseCard.scss';

export default function showcaseCard(props) {
  const { showcase } = props;
  const {cover, title, url, screenshot, desc, technologies, partners} = showcase;
  return (
    <div className="showcase-card">
      <Card>
        <Card.Img variant="top" src={cover} />
        <Card.Body>
          <Card.Title>{title}</Card.Title>
          <Card.Text>
            {desc}
          </Card.Text>
        </Card.Body>
        <ListGroup className="list-group-flush">
          <ListGroupItem>
            <div>Technologies:</div>
            <div className="technologies">{technologies.join(", ")}</div>
          </ListGroupItem>
          <ListGroupItem>
            <div>Participants:</div>
            <div>
              {partners.map((p) => <div key={p.name}><a href={`https://about.blilac.ir/${p.slug}`}>{p.name}</a></div>)}
            </div>
          </ListGroupItem>
        </ListGroup>
        <Card.Body>
          {
            url &&
            <Card.Link href={url}>
              <Button variant="outline-dark">Source Code</Button>
            </Card.Link>
          }
          <Card.Link href={screenshot}>
            <Button variant="outline-info">Project URL</Button>
          </Card.Link>
        </Card.Body>
      </Card>
    </div>
  );
}
showcaseCard.propTypes = {
  showcase: PropTypes.shape({
    cover: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    desc: PropTypes.string.isRequired,
    technologies: PropTypes.arrayOf(PropTypes.string).isRequired,
    partners: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string.isRequired,
      slug: PropTypes.string.isRequired,
    })).isRequired
  }),
}